import React, { useState } from 'react';
import Modal from 'react-modal';
import PostDetail from './PostDetail';
import ThumbImg from './ThumbImg';
import Tag from './Tag';

const PostLink = ({ node, isDesktop, isMobile }) => {
  const [modalIsOpen, setIsOpen] = useState(false);

  const modalStyle = {
    content: {
      position: 'absolute',
      backgroundColor: 'rgba(255, 255, 255, 1)',
      maxWidth: '1200px',
      margin: 'auto',
      height: '90%',
      overflowY: 'scroll',
      boxShadow: '0 0 10px #ddd',
    },
  };

  return (
    <>
      {/* デスクトップ用 */}
      {isDesktop && (
        <>
          <button className="post-link-button" onClick={() => setIsOpen(true)}>
            <ThumbImg id={node.thumb.id} isMobile={false} />
            <div className="post-thumb-mask">
              <p className="post-thumb-caption">{node.title}</p>
              <p className="post-thumb-date">{node.date}</p>
              <div className="post-thumb-tags">
                <Tag tags={node.tag} />
              </div>
            </div>
          </button>
          {/* ここからモーダル */}
          <Modal
            isOpen={modalIsOpen}
            style={modalStyle}
            onAfterOpen={() => (document.body.style.overflow = 'hidden')}
            onAfterClose={() => (document.body.style.overflow = 'auto')}
            onRequestClose={() => setIsOpen(false)}
          >
            <PostDetail node={node} isDesktop={true} close={() => setIsOpen(false)} />
          </Modal>
        </>
      )}
      {/* モバイル用 */}
      {isMobile && (
        <a className="post-link-button" href={'/post/' + node.id + '/'}>
          <h3 className="color-text">{node.title}</h3>
          <div className="post-image">
            <ThumbImg id={node.thumb.id} isMobile={false} />
          </div>
        </a>
      )}
    </>
  );
};

export default PostLink;
