import React from 'react';
import Fade from 'react-reveal/Fade';
import { Container } from 'react-bootstrap';
import Title from '../components/Title';

const About = ({ isDesktop, isMobile }) => {
  return (
    <section id="about">
      <Container>
        {isDesktop && (
          <>
            <Title title={'Yuki Nishidate / 西舘祐樹'} />
            <Fade right duration={500} delay={250} distance="30px">
              <p>{'コンピュータグラフィックスが好きな大学生です。'}</p>
              <br></br>
            </Fade>

            <Fade right duration={500} delay={350} distance="30px">
              <h2>{'学歴'}</h2>
              <p>{'・ 慶應義塾湘南藤沢高等部 卒業'}</p>
              <p>{'・ 慶應義塾大学 理工学部 情報工学科 在学中'}</p>
              <br></br>
            </Fade>

            <Fade right duration={500} delay={450} distance="30px">
              <h2>{'職歴'}</h2>
              <p>{'・ 家具広告制作会社で3Dデザイナー'}</p>
              <p>{'・ デジタル・フロンティアでR&Dエンジニア'}</p>
              <br></br>
            </Fade>

            {/* <Fade right duration={500} delay={550} distance="30px">
              <h2>{'業績'}</h2>
              <p>{'・ DHU U18アーティストコンテストで準優勝'}</p>
              <p>{'・ CGエンジニア検定の成績に基づいて文部科学大臣賞受賞'}</p>
              <p>{'・ 研究業績については今後に期待...'}</p>
            </Fade> */}
          </>
        )}
        {isMobile && (
          <>
            <Title title="Yuki Nishidate" />
            <p>
              {
                '慶應義塾大学 理工学部 情報工学科の学部生です。コンピュータグラフィックスに興味があります。'
              }
            </p>
          </>
        )}
      </Container>
    </section>
  );
};

export default About;
