import React, { useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import { useStaticQuery, graphql } from 'gatsby';
import PostLink from '../components/PostLink';
import Title from '../components/Title';
import TagList from '../components/TagList';

const Projects = ({ isDesktop, isMobile }) => {
  const data = useStaticQuery(
    graphql`
      query {
        posts: allContentfulPost(sort: { fields: date, order: DESC }) {
          nodes {
            id
            images {
              id
              fluid(maxWidth: 1000) {
                src
                ...GatsbyContentfulFluid
              }
            }
            videos {
              file {
                url
              }
            }
            title
            date(formatString: "yyyy / M")
            description {
              description
            }
            url
            thumb {
              id
              fluid(maxWidth: 1000) {
                src
                ...GatsbyContentfulFluid
              }
            }
            tag
          }
        }
      }
    `
  );

  var tagMap = {};
  data.posts.nodes.forEach((node) => {
    node.tag &&
      node.tag.forEach((tag) => {
        tagMap[tag] = false;
      });
  });

  const [tags, setTags] = useState(tagMap);

  function toggleTags(tag) {
    if (tags[tag]) {
      tagMap[tag] = false;
    } else {
      tagMap[tag] = true;
    }
    setTags(tagMap);
  }

  function isFiltered() {
    for (var key in tags) {
      if (tags[key]) {
        return true;
      }
    }
    return false;
  }

  function isMatch(node) {
    var match = false;
    if (node.tag) {
      node.tag.forEach((tag) => {
        if (tags[tag]) {
          // tag is enabled
          match = true;
        }
      });
    }
    return match;
  }

  return (
    <section id="projects">
      <Container>
        <Title title={'All Posts'}></Title>
        {isDesktop && (
          <>
            <TagList nodes={data.posts.nodes} toggleTags={toggleTags} tagMap={tags} />
            <Row md={3}>
              {data.posts.nodes.map((node) => {
                if (isFiltered()) {
                  if (isMatch(node)) {
                    return (
                      <Fade right duration={500} delay={250} distance="30px">
                        <Col>
                          <PostLink node={node} isDesktop={isDesktop} isMobile={isMobile} />
                        </Col>
                      </Fade>
                    );
                  } else {
                    return <></>;
                  }
                } else {
                  return (
                    <Fade right duration={500} delay={250} distance="30px">
                      <Col>
                        <PostLink node={node} isDesktop={isDesktop} isMobile={isMobile} />
                      </Col>
                    </Fade>
                  );
                }
              })}
            </Row>
          </>
        )}
        {isMobile && (
          <>
            {data.posts.nodes.map((node) => {
              return <PostLink node={node} isDesktop={isDesktop} isMobile={isMobile} />;
            })}
          </>
        )}
      </Container>
    </section>
  );
};

export default Projects;
