import React from 'react';

function getTagList(nodes) {
  var tags = [];
  nodes.forEach((node) => {
    node.tag &&
      node.tag.forEach((tag) => {
        tags && !tags.includes(tag) && tags.push(tag);
      });
  });
  return tags;
}

const TagList = ({ nodes, toggleTags, tagMap }) => {
  var tagList = getTagList(nodes);
  return (
    <div className="taglist">
      {tagList &&
        tagList.map((tag) => {
          return (
            <button onClick={() => toggleTags(tag)}>
              <p className={tagMap[tag] ? 'enable-tag' : 'tag'}>{tag}</p>
            </button>
          );
        })}
    </div>
  );
};

export default TagList;
